import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Movie from '@/components/movie/Movie' 
import MovieWill from '@/components/movie/MovieWill' 
import MovieList from '@/components/movie/MovieList'
import MovieDetail from '@/components/movie/MovieDetail'
import Place from '@/components/place/Place'
import MoviePlace from '@/components/place/MoviePlace'
import Watch from '@/components/watch/Watch'
import WatchList from '@/components/watch/WatchList'
import WatchMusic from '@/components/watch/WatchMusic'
import PhotoList from '@/components/watch/PhotoList'
import PhotoDetail from '@/components/watch/PhotoDetail'
import My from '@/components/my/My'
import MyList from '@/components/my/MyList'
import Deng from '@/components/deng/Deng'
import DengDetail from '@/components/deng/DengDetail'


Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: '/movie/movielist'
    },{
      path: '/movie',
      name: 'Movie',
      component: Movie,
      children:[ 
        {
          path: 'movielist',
          name: 'movielist',
          component: MovieList,
        },{
          path: 'moviewill',
          name: 'moviewill',
          component: MovieWill,
        },{
          path: 'movieDetail/:movieId',
          name: 'MovieDetail',
          component: MovieDetail,
        }
      ]
    },{
      path: '/place',
      name: 'Place',
      component: Place,
      redirect: '/place/movieplace',
      children:[
        {
        path: 'movieplace',
        name: 'MoviePlace',
        component: MoviePlace,
      }
      ]
    },{
      path: '/watch',
      name: 'Watch',
      component: Watch,
      redirect: '/watch/watchlist',
      children:[{ 
        path: 'watchlist',
        name: 'Watchlist',
        component: WatchList
      },{ 
        path: 'watchmusic',
        name: 'Watchmusic',
        component: WatchMusic
      },{ 
        path: 'photolist',
        name: 'PhotoList',
        component: PhotoList
      },{ 
        path: 'PhotoDetail/:index',
        name: 'PhotoDetail',
        component: PhotoDetail
      }
      ]
    },{
      path: '/my',
      name: 'My',
      component: My,
      redirect: '/my/mylist',
      children:[{ 
        path: 'mylist',
        name: 'MyList',
        component: MyList
      }
      ]
    },{
      path: '/deng',
      name: 'Deng',
      component: Deng,
      redirect: '/deng/deng',
      children:[
        {
        path: 'dengdetail',
        name: 'DengDetail',
        component: DengDetail,
      }
      ]
    },
  ]
})



