<template>
    <ul class="watch">
        <li v-for="watch in watchList" :key="watch.num">
            <router-link to="">
                <video controls>
                    <source :src="watch.play">
                </video>
                <p>{{watch.title}}</p>
            </router-link>
            <div class="icon">
                <img src="/static/img/zan.png" alt="">
                <img src="/static/img/pinglun.png" alt="">
            </div>
            
        </li>
    </ul>
</template>

<script>
import Axios from 'axios'
export default {
    data(){
        return {
            watchList: []
        }
    },
    mounted(){
        let url = './static/data/hotdata.json'

        Axios.get(url).then(res => {
            this.watchList = res.data.data
            console.log(res)
            //将数据存入vuex
            // this.$store.dispatch('actionPhotoList', this.photoList);
           // console.log(this.photoList);
        }).catch()
    }
}
</script>

<style scoped>
@import url(../../assets/css/common.css);
.watch{margin: 1.3rem 0;}
.watch li{width: 90%;margin: 5% auto;position: relative;}
.watch li a{color: #000;outline: none;}
.watch li a p{font-size: 0.3rem;margin-top: 2%;}
.watch li:nth-of-type(1){display: none;}
.icon img{width: 6%;position: absolute;bottom: 5%;}
.icon img:nth-of-type(1){right: 15%;}
.icon img:nth-of-type(2){right: 5%;}
video{width: 98%;}

</style>



