<template>
   <v-touch class="detail" :style="styleObj" @swipeleft='swipeLeft' @swiperight='swipeRight' @tap='tap'></v-touch>
</template>

<script>
export default {
    data(){
        return {
            nowIndex:0
        }
    },
    mounted(){       
        console.log(this.$store.state.photoList[this.$route.params.index].src);        
        this.nowIndex=this.$route.params.index;
        console.log(this.nowIndex)
    },
    computed:{
        styleObj(){
            return {
                background: `#F3F3F3 url(${this.$store.state.photoList[this.nowIndex].src}) no-repeat center/contain`}
            }
    },
    methods:{
        swipeLeft(){
            this.nowIndex++;
            if(this.nowIndex==this.$store.state.photoList.length){
                this.nowIndex=0;
            }
            console.log('left')
        },
        swipeRight(){
            this.nowIndex--;
            if(this.nowIndex<=0){
                this.nowIndex=this.$store.state.photoList.length-1;
            }
            console.log('right')
        },                                             
        tap(){
            this.$router.go(-1)
            console.log('tap')
        },
    }
}
</script>

<style scoped>
div.detail{margin:1rem 0; position:absolute; top:0; right:0; bottom:0; left:0;}
</style>


