<script>
export default {
 props:['bg']
}
</script>

<style scoped>
.header{width: 100%;height: 1rem;line-height: 1rem;background: #D84C29;color:#fff;position: fixed;top: 0;display: flex;justify-content: center;z-index: 1000;}
.header li{margin-left: 0.5rem}
.header li a{color:#fff;font-size: 0.25rem;}
.header li a.router-link-active{outline: none;font-weight: bold;font-size: 0.35rem;}
.header li a img{width: 40%;display: flex;flex-wrap: wrap;margin: 0.1rem 0 -0.3rem 0;}
