<template>
    <div>
      <ul class="header">
            <li><router-link to=""><img src="/static/img/logo.ico" alt=""></router-link></li>
            <li><router-link to="/place/movieplace">
              <select>
                <option>城市</option>
              </select>
              </router-link>
            </li>
            <li><router-link to="">电影</router-link></li>
            <li><router-link to="">影院</router-link></li>
        </ul>
        <ul class="title">
          <li><router-link to="/movie/movielist">正在热映</router-link></li>
          <li><router-link to="/movie/moviewill">即将上映</router-link></li>
        </ul>

        <swipe class="my-swipe">
                <swipe-item class="slide"><img src="/static/img/p1.jpg" alt=""></swipe-item>
                <swipe-item class="slide"><img src="/static/img/p2.jpg" alt=""></swipe-item>
                <swipe-item class="slide"><img src="/static/img/p3.jpg" alt=""></swipe-item>
        </swipe>
        <ul class="movie-list">
          <li @click="goDetail(movie.id)" v-for="movie in MovieList" :key='movie.id' class="movie">
              <div class="movie-img">
                <img :src="movie.img">
              </div>
              <div class="movie-info">
                <p>{{movie.nm}}</p>
                <p>{{movie.ver}}</p>
                <p>主演：{{movie.star}}</p>
                <p>{{movie.showInfo}}</p>
              </div>
              
              <p class="buy"><router-link to="">购票</router-link></p>
          </li>
          <div class="loading" v-show="loadshow">
            <img src="@/assets/img/loading.gif">
          </div>
          <div class="tip" v-show="tip">
              <h4>--到头啦!--</h4>  
          </div>
        </ul>
    </div>
</template>

<script>
import Axios from 'axios'
import { Swipe, SwipeItem } from "vue-swipe";
import "vue-swipe/dist/vue-swipe.css";

export default {
  data(){
    return{
      MovieList:[],
      loadshow: false,
      tip:false
    }
  },
  components:{
      "swipe": Swipe,
      "swipe-item": SwipeItem
  },
  methods:{
      loadData(){
      /* let url1=API_PROXY + "http://api.douban.com/v2/movie/top250" */
      let url1="/static/data/moviedata.json"
      Axios.get(url1).then((res)=>{
        let list=res.data.data.movies;
        let data=list.slice(this.MovieList.length,this.MovieList.length + 5);

        if(data.length < 5){
            this.tip = true;
          }
          this.MovieList = this.MovieList.concat(data);
      }).catch()
    },
    goplace(){
      this.$router.push('/place/movieplace');
    },
    goDetail(movieId){
      this.$router.push('/movie/movieDetail/' + movieId);
    }
  },
  mounted(){
    this.loadData()
    window.onscroll=()=>{
      let clientHeight=document.documentElement.clientHeight;
      let scrollTop=document.documentElement.scrollTop;
      let scrollHeight=document.documentElement.scrollHeight;

      if(scrollTop+clientHeight>=scrollHeight-110){
        console.log('这里是世界尽头: '+this.tip, this.loadShow);
        this.loadShow = true; //当前页数据获取完毕后加载动画
        if(!this.tip){
          this.loadData();   
        }else{
          this.loadShow = false;
        }
      }
    }
  },
}
</script>

<style scoped>
.header{width: 100%;height: 1rem;line-height: 1rem; background: #fff;position: fixed; top: 0;border-bottom: 1px solid rgb(245, 242, 242);display: flex;justify-content: space-around;z-index: 1000;}
.header li a{font-size: 0.3rem;}
.header li a.router-link-active{outline: none;color:rgb(240, 106, 17);font-weight: bold;}
.header li:first-child{margin:0 -15% 0 -4%;}
.header li:first-child img{vertical-align: middle;}
.header li select{border: none;outline: none;background: #fff;} 
.header li:last-child{margin:0 20% 0 -10%;}

.title{width: 100%;height: 1rem;line-height: 1rem; background: #fff;position: fixed; top: 1rem;display: flex;justify-content: space-around;z-index: 1000;}
.title a{color: #E54847;font-size: 0.3rem;;}
.title a.router-link-active{outline: none;font-weight: bold;}
.my-swipe {margin: 2rem 0 0 0;height: 3rem;color: #fff;font-size: 30px;text-align: center;}
.slide {background-size: 100%;color: #fff;}
.movie{display: flex;padding: 0.2rem;border-bottom: 1px solid #ccc;}
.movie-img{flex:1 1 0;padding-right: 0.2rem;}
.movie-info{flex:2 1 0;}
.movie-info p:first-child{font-weight: bold;color: #171312;font-size: 0.3rem;}
.loading{text-align: center;}
.tip{font-size: 0.1rem;text-align: center;color: #333;}
.buy{width: 14%;height: 5%;background: #E54847;text-align: center;margin: 1rem 0.2rem;border-radius: 0.1rem;}
.buy a{color: #fff;}
</style>


