<template>
    <div class="detail">
        <!-- <h2>MovieDetail: {{$route.params.movieId}}</h2> -->
      <div class="fan">
        <router-link to="/movie/movielist"><img src="/static/img/fanhui.png" alt="">返回
      </router-link></div>
      
      <div class="detail-content">
        <div class="detail-img" >
          <img :src="movieDetail.img" alt="加载失败">
        </div>
        <div class="detail-info">
          <h2 v-text="movieDetail.nm"></h2>
          <p v-text="movieDetail.sc+' 分'"></p>
          <p>{{movieDetail.cat}}</p>
          <p>{{movieDetail.src}} / {{movieDetail.dur}}</p>
          <p>{{movieDetail.rt}}</p>
        </div>
      </div>
      <hr />
        <div>
            <h3>简介</h3>
            <p>{{movieDetail.dra}}</p>
        </div>
        <hr />
        <div>
            <h3>主演</h3>
            <p>{{movieDetail.star}}</p>
        </div>
    </div>
    
</template>

<script>
import Axios from 'axios'

export default {
  name:'',
  data(){
   return {
     movieDetail:{},
   }
  },
  mounted(){
    let url2="/static/data/moviedetail.json"
    Axios.get(url2).then((res)=>{
      this.movieDetail = res.data.data.MovieDetailModel;
    }).catch()
  }
}
</script>

<style scoped>
.detail{padding:0.2rem;}
.fan{height: 1rem;}
.fan a{color: #000;}
.fan img{width: 5%;vertical-align: middle;}
.detail-content{ display:flex; margin-bottom: 0.2rem;background: #fff;}
.detail-img{ flex: 1 1 0; margin-right: 0.2rem;}
.detail-info{flex: 2 1 0;}
.detail h3{ text-align: center;}
</style>



