<template>
    <div>
        <div class="header">
            <span class="home" @click="gohome">关闭</span>
            <span class="title">选择城市</span>
            <input type="text" placeholder="请输入城市名或拼音" class="search">
        </div>
        
        <ul class="place">
            <router-link to="/movie/movielist"><li v-for="place in MoviePlace" :key="place.id">
                
                    {{place.FullName}}
                
            </li></router-link>
        </ul>
    </div>
</template>

<script>
import Axios from 'axios'
export default {
    data(){
        return{
            MoviePlace:[],
        }
    },
    methods:{
      gohome(){
          this.$router.push('/')
      }
    },
    mounted(){
        let url = './static/data/place.json'
        Axios.get(url).then(res => {
            this.MoviePlace = res.data.result
             //将数据存入vuex
            //this.$store.dispatch('actionPhotoList', this.photoList);
           // console.log(this.photoList);
        }).catch()
    }

}
</script>

<style scoped>
.header{width: 100%;height: 0.8rem;line-height: 0.8rem;position: fixed;top: 0;left:0;background: #FAFAFA;}
.home{margin-left: 0.2rem;}
.title{font-size: 0.3rem;position: absolute;top: 50%;left: 50%;transform: translate(-50%,-50%);}
.place{margin: 1.2rem 0 0 0;}
.place a{color: #000;outline: none;margin-left: 0.2rem;}
.place li{background: #FAFAFA;border-bottom: 0.8px solid #ccc;height: 0.8rem;line-height: 0.8rem;}
.search{width: 100%;background: #fff;background-position: 0.2rem; height: 0.8rem;border: none;padding-left: 0.6rem;}
</style>




